/* thin*/
@font-face {
  font-family: "Primera";
  src: url("./../assets/fonts/custom/primera.eot");
  src: local("0"),
    url("./../assets/fonts/custom/primera.ttf") format("truetype"),
    url("./../assets/fonts/custom/primera.eot") format("eot");
}

@font-face {
  font-family: "Primera Italic";
  src: url("./../assets/fonts/custom/primeraIt.eot");
  src: local("0"),
    url("./../assets/fonts/custom/primeraIt.ttf") format("truetype"),
    url("./../assets/fonts/custom/primeraIt.eot") format("eot");
}

